/* 外层卡片：固定高度 + 垂直布局 + 整齐对齐 */
.card {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 260px;
  height: 260px;                  /* ★ 固定卡片高度 */
  margin: 12px;

  background: var(--background);
  border-radius: var(--rounded);
  box-shadow: var(--shadow);
  overflow: hidden;
}

/* small 样式 */
.card[data-style="small"] {
  width: 240px;
  height: 240px;
}

/* 图片区域：固定高度 */
.card-image {
  width: 100%;
  height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 6px;
}

.card-image img {
  max-height: 80px;
  object-fit: contain;
}

/* 文本区域：固定布局 + 禁止单词分散拉伸 */
.card-text {
  flex: 1;
  width: 100%;
  padding: 12px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  text-align: center;
  text-align-last: center;

  font-size: 0.98rem;

  white-space: normal !important;       /* ★ 修复英文单词被拉开 */
  word-spacing: normal !important;      /* ★ 修复间距问题 */
  letter-spacing: normal !important;
}

.card-text > * {
  margin: 0 !important;
}

/* 标题：固定高度 + 字体变大 + 禁止拉伸 */
.card-title {
  font-family: var(--heading);
  font-weight: var(--semi-bold);

  height: 48px;
  overflow: hidden;
  text-align: center;

  white-space: normal !important;
  word-spacing: normal !important;
  letter-spacing: normal !important;

  font-size: 1.05rem;
}

/* 链接标题 */
.card-text a {
  font-size: 1.05rem;
  text-align: center;

  white-space: normal !important;
  word-spacing: normal !important;
  letter-spacing: normal !important;
}

/* 副标题（年份）—— 修复英文单词变宽的问题 */
.card-subtitle {
  font-style: italic;
  margin: 0 !important;

  height: 22px;
  line-height: 22px;

  text-align: center;
  text-align-last: center;

  white-space: normal !important;
  word-spacing: normal !important;
  letter-spacing: normal !important;

  font-size: 0.95rem;
}
